// app/providers.tsx
"use client";

// Đã xoá: import "../lib/serverStoragePolyfill";

import { ReactNode, useMemo } from "react";
import {
  WagmiProvider,
  createConfig,
  http,
  createStorage, // Thêm
  cookieStorage, // Thêm
} from "wagmi";
import { xLayer } from "wagmi/chains"; // Thêm chain xlayer
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import {
  RainbowKitProvider,
  darkTheme,
  connectorsForWallets,
} from "@rainbow-me/rainbowkit";
import {
  okxWallet,
  metaMaskWallet,
  walletConnectWallet,
  rainbowWallet,
  rabbyWallet,
  trustWallet,
  bitgetWallet,
  coinbaseWallet,
} from "@rainbow-me/rainbowkit/wallets";
// Đã xoá: type { Chain } from "viem"; (không cần nữa vì ta import xlayer)
import "@rainbow-me/rainbowkit/styles.css";

// ==== ENV (đổi lại nếu cần) ====
const WC_PROJECT_ID =
  process.env.NEXT_PUBLIC_WC_PROJECT_ID || "df8d376695ef6244fbb2accd6a85f00a";
const RPC = process.env.NEXT_PUBLIC_RPC_URL || "https://xlayerrpc.okx.com";

// ==== Đã xoá phần khai báo chain XLayer thủ công ====

// ==== Connectors (v2) (Giữ nguyên) ====
const APP_NAME = "BANMAO";
const APP_DESCRIPTION = "BANMAO community portal";
const APP_URL = process.env.NEXT_PUBLIC_APP_URL ?? "https://banmao-fun-v2.vercel.app/";
const APP_ICON =
  process.env.NEXT_PUBLIC_APP_ICON ?? `${APP_URL.replace(/\/$/, "")}/banmao_logo.png`;

const connectors = connectorsForWallets(
  [
    {
      groupName: "Recommended",
      wallets: [okxWallet, metaMaskWallet, walletConnectWallet],
    },
    {
      groupName: "More wallets",
      wallets: [rainbowWallet, rabbyWallet, trustWallet, bitgetWallet, coinbaseWallet],
    },
  ],
  {
    appName: APP_NAME,
    appDescription: APP_DESCRIPTION,
    appUrl: APP_URL,
    appIcon: APP_ICON,
    projectId: WC_PROJECT_ID,
  }
);

// ==== wagmi config (v2) ====
const config = createConfig({
  chains: [xLayer], // Dùng chain xlayer đã import
  connectors,
  transports: {
    [xLayer.id]: http(RPC, { batch: true }),
  },
  ssr: true,
  // Thêm storage để xử lý SSR với Next.js
  storage: createStorage({
    storage: cookieStorage,
  }),
  multiInjectedProviderDiscovery: false,
});

const queryClient = new QueryClient();

export default function Providers({ children }: { children: ReactNode }) {
  const theme = useMemo(
    () =>
      darkTheme({
        accentColor: "#FFD700",
        accentColorForeground: "#000",
        borderRadius: "medium",
        overlayBlur: "small",
      }),
    []
  );

  return (
    <WagmiProvider config={config}>
      <QueryClientProvider client={queryClient}>
        <RainbowKitProvider theme={theme} modalSize="compact" initialChain={196}>
          {children}
        </RainbowKitProvider>
      </QueryClientProvider>
    </WagmiProvider>
  );
}